import React, { Component } from "react";
import "../scss/stage.scss";
import image from "../images/데이터하이브.PNG";

class StageContents extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <>
        <section className="banner">
          <div className="title">
            <h1>스테이지에 올린 게시물을 작성해보세요!</h1>
            <h2>설문지 형태로 진행됩니다</h2>
          </div>
          <div className="btn_contain">
            <div className="btn">바로가기</div>
          </div>
        </section>

        <section className="content_container">
          <div className="contents">
            <div className="img">
              <img src={image} alt="" />
            </div>
            <div className="content">
              <div className="title">
                AI / 빅데이터 사업을 확장하고 싶으세요? 데이터바우처 사업을
                더그로우즈와 함께!!
              </div>
              <div className="company_name">데이터하이브</div>
              <div className="info_contain">
                <div className="period">2021-07-14</div>
                <div className="view_like">
                  <div className="view">
                    <i className="fas fa-eye"></i>100
                  </div>
                  <div className="like">
                    <i className="far fa-thumbs-up"></i>200
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default StageContents;
